import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import PaginationItem from "@mui/material/PaginationItem";

const dispatchedEvents = [];

const SrsMuiPaginationItem = function (props, ref) {
  return (
    <PaginationItem
      {...props}
      theme={props.muiTheme}
    >
      <slot />
    </PaginationItem>
  );
}

const styles = `
  /* Custom styles for the MuiPaginationItem component */
`;

const props = [
  {
    name: "page",
    defaultValue: null,
    type: "number",
  },
  {
    name: "selected",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "href",
    defaultValue: null,
    type: "string",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiPaginationItem if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiPaginationItem), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
